<template>
  <div class="notice">
    <div class="header">消息列表</div>
    <div class="content">
      <ul class="notice-list" v-if="noticeData">
        <NoticeItem v-for="(item, index) in noticeData" :key="index" :item="item" />
        <li class="end" v-if="!isLoading" @click="MoreNotice">{{endText}}</li>
      </ul>
      <div class="loading" v-if="isLoading">{{loadingText}}</div>
    </div>



  </div>
</template>

<script>
  import Vue from 'vue'
  import NoticeItem from '@/components/NoticeItem.vue'
  export default {
    name: 'notice',
    data() {
      return {
        noticeData: null,
        nextUrl: null,
        loadingText: '加载中',
        isLoading: true

      }
    },
    methods: {
      MoreNotice() {
        if (!this.nextUrl) {
          return
        }
        this.isLoading = true
        this.axios.get(`https:api.kele8.cn/agent/${this.nextUrl}`)
          .then(res => {
            let arr = res.data.messageList
            this.noticeData.push(...arr)
            this.nextUrl = res.data.nextPageUrl
          })
          .finally(() => {
            this.isLoading = false
          })
      }
    },
    computed: {
      endText() {
        return this.nextUrl ? '加载更多' : '到底咯~'
      }
    },
    beforeRouteEnter(to, from, next) {
      Vue.axios.get(`https:api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/messages`)
        .then(res => {
          next(vm => {
            vm.noticeData = res.data.messageList
            vm.nextUrl = res.data.nextPageUrl
            vm.isLoading = false
          })
        })

    },
    components: {
      NoticeItem
    }

  }
</script>

<style lang='less' scoped>
  .notice {
    .header {
      text-align: center;
      background-color: #fafafa;
      padding: 10px 0;
      font-weight: bold;
      color: #6b6b6b;
      width: 100%;
      position: fixed;
      top: 0;
      left: 0;
    }

    .content {
      padding: 50px 15px 55px 10px;

      .notice-list {
        padding: 0;

        .end {
          text-align: center;
          color: #666;
          padding-bottom: 20px;
        }
      }

      .loading {
        text-align: center;
        color: #666;
        padding-bottom: 20px;

      }
    }


  }
</style>